.card {
    /* width: 500px; */
    /* height: 200px; */
    background-color: #ede9e9;
    border-radius: 4px;
    width: 100%;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin: 10px;
  }
  
  .card-title {
    font-size: 10px;
    font-weight: bold;
    margin: 10px 0;
    padding: 10px;
    border-bottom: 1px solid #ddd;
  }
  
  .card-content {
    padding: 10px;
  }
  
  .country-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .country-list li {
    font-size: 14px;
    margin-bottom: 5px;
  }